
















































































import { defineComponent, ref } from "@vue/composition-api";
import { OrderProduct } from "@/types";
import { ApiService } from "@/services/apiService";

export default defineComponent({
  name: "ProductDetails",
  props: {
    item: {
      type: Object,
      required: true
    }
  },

  setup(props, { emit }) {
    const panel = ref(0);
    const readonly = ref(false);

    function handleBackButton() {
      emit("on-back-button");
    }

    const apiService = new ApiService();

    const valid = ref(true);
    const form = ref(null);
    const name = ref("");
    const email = ref("");
    const phone = ref("");
    const remark = ref("");

    const nameRules = ref([
      (v) => !!v || "Name is required",
      (v) => (v && v.length <= 10) || "Name must be less than 10 characters"
    ]);
    const emailRules = ref([
      (v) => !!v || "E-mail is required",
      (v) => /.+@.+\..+/.test(v) || "E-mail must be valid"
    ]);
    const phoneRules = ref([
      (v) => !!v || "Phone Number is required",
      (v) => (v && v.length <= 15) || "Phone Number must be valid"
    ]);
    const remarkRules = ref([
      (v) => !!v || "Remark is required",
      (v) => (v && v.length <= 5) || "Remark must be less than 5 characters"
    ]);

    const submit = async () => {
      var isValid = form.value.validate();
      if (isValid) {
        var order = {
          referloId: 1,
          contactName: name.value,
          contactPhone: phone.value,
          contactEmail: email.value,
          supplierProductId: props.item.supplierProductId,
          pointsDefault: props.item.points
        } as OrderProduct;
        var responseOrder = await apiService.makeOrder(order);
        console.log(responseOrder);
      }
    };
    const reset = () => {
      form.value.reset();
    };

    return {
      handleBackButton,
      panel,
      readonly,
      valid,
      form,
      name,
      email,
      phone,
      remark,
      nameRules,
      emailRules,
      phoneRules,
      remarkRules,
      submit,
      reset
    };
  }
});
